.fade-out {
  -webkit-animation: fadeOut ease-in 400ms;
          animation: fadeOut ease-in 400ms;
}

@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}



